@import '../../../../includes/assets/css/consts.scss';



.dp-tmce-dialog {
	@include z-index(6, true);

	&__backdrop {
		@include z-index(5, true);
	}
}


html.dpit-bon {
	#wp-link-wrap {
		opacity: 1;
		@include z-index(9, true);
	}

	#wp-link-backdrop {
		@include z-index(9, true);
	}

	.wp-link-text-field {
		visibility: hidden !important;
		position: absolute;
		pointer-events: none;
		//display: none !important;
	}
}


