.mce-container.mce-panel.mce-menu {
	@include z-index(7, true);
}

.mce-notification,
div.mce-inline-toolbar-grp,
.mce-container.mce-floatpanel {
	@include z-index(7, true);
}

.media-modal.wp-core-ui {
	@include z-index(8, true);
}