@import '../consts.scss';



.dp-tmce-dialog {
	background-color: rgba(var(--dpu-color-1-text), 0.9); // $background-color-light;

	padding: 20px;
	position: fixed;
	border-radius: 2px;
	z-index: 100000;
	box-shadow: 2px 2px 8px 0 rgba(33, 33, 33, 0.4);
	display: none;
	min-width: 600px;

	@include transitionMulti($transition-time-slow, ease-in-out, width, height, top, left);
	//@include transitionMultiFast(width, height, top, left);



	@include atS {
		min-width: unset;
		border-radius: 0;
	}

	.wp-editor-tools {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		width: 100%;

		&::after {
			display: none;
		}
	}

	.wp-media-buttons .button:not(.add_media) {
		display: none;
	}

	button.insert-media {
		margin-bottom: 10px;

	}


	button.insert-media,
	button.wp-switch-editor {

		text-transform: uppercase;
		font-weight: 700;
		color: rgb(var(--dpu-color-1));
		border: 1px solid rgba(var(--dpu-color-1), 0.7);
		background-color: transparent;
		@include transitionMultiFast(background-color, color);
	}



	button.insert-media:active {
		margin-bottom: 10px;
	}

	button.insert-media:hover {
		color: rgb(var(--dpu-color-4-text));
		background: linear-gradient(315deg, rgba(var(--dpu-color-4), 0.95) 0%, rgba(var(--dpu-color-4), 0.9) 20%, rgba(var(--dpu-color-4), 0.96) 100%) !important;

	}

	button.wp-switch-editor {
		display: block;
		border-bottom-color: transparent;

		&:not(:hover) {
			color: rgba(var(--dpu-color-1), 0.7);
		}


	}

	.html-active button#dp-tmce-editor-html,
	.tmce-active button#dp-tmce-editor-tmce {
		//background-color: rgb(var(--dpu-color-4));
		background: linear-gradient(315deg, rgba(var(--dpu-color-4), 0.95) 0%, rgba(var(--dpu-color-4), 0.9) 20%, rgba(var(--dpu-color-4), 0.96) 100%) !important;
		color: rgb(var(--dpu-color-4-text));
	}


	&__title {
		font-size: 25px;
		line-height: 1.2;
		padding-bottom: 17px;
		font-family: var(--btn_font);
		font-weight: 700;
		text-transform: uppercase;
		color: rgba(var(--dpu-color-1), 0.95);
		margin-right: 20px; // close button

	}

	&__close {
		position: absolute;
		right: 20px;
		top: 10px;
		font-size: 30px;
		opacity: 0.8;
		transition: opacity $transition-time-fast ease-in-out;
		cursor: pointer;
		color: rgba(var(--dpu-color-1));

		&:hover {
			opacity: 1;
		}
	}

	&__btn-wrap {
		text-align: right;
	}

	&__btn {
		margin: 0 0 0 auto;
		display: inline-block;
		min-width: 130px;
		text-align: center;
		font-family: var(--btn_font);
		font-weight: 700;
		text-transform: uppercase;
		font-size: var(--h4_font_size) !important;
		letter-spacing: 1.5px;
		color: rgb(var(--dpu-color-4-text)) !important;
		padding: 12px 30px !important;
		background: linear-gradient(315deg, rgba(var(--dpu-color-4), 0.95) 0%, rgba(var(--dpu-color-4), 0.89) 20%, rgba(var(--dpu-color-4), 0.94) 100%) !important;
		//background: var(--dpu-color-4) !important;
		cursor: pointer;
		border-radius: 2px;
		@include transitionFast(background);

		&:hover {
			background: linear-gradient(315deg, rgb(var(--dpu-color-4)) 0%, rgba(var(--dpu-color-4), 0.97) 20%, rgb(var(--dpu-color-4)) 100%) !important;

		}
	}

	&__backdrop {
		z-index: 99999;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		position: fixed;
		//background: $text-color-dark;
		background-color: rgba(var(--dpu-color-1), 0.9);
		opacity: 0.95;
		pointer-events: all;
		display: none;
		@include transition(background-color);
	}

	&__content {

		.mce-btn.mce-active:hover,
		.mce-btn.mce-active {
			i {
				color: #50575e;
			}
		}


	}

	.mce-btn .mce-txt {
		//color: rgba(var(--dpu-color-1), 0.9);
	}

}

body:not(.mce-fullscreen) .dp-tmce-dialog {
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	@include atS {
		top: 0;
		left: 0;
		transform: unset;
		right: 0;
		bottom: 0;
	}
}