.noscroll {
	//position: fixed; 
	//overflow-y:scroll;
	//width: 100%;
}

#wp-dpwplink-editor-public-wrap {
	opacity: 0;
	position: absolute;
}

.mce-container {
	display: none;
}

#dp_wp-link {
	position: relative;
	height: 100%;
}



#dp_wp-link-backdrop {
	opacity: 0;
	pointer-events: none;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	min-height: 360px;
	background: #000;
	z-index: 0;
	transition: opacity 0.3 ease;
	display: block !important;

	&.active {
		z-index: 100100;
		opacity: 0.7;
		pointer-events: all;
	}
}


#dp_wp-link-wrap.dp_wp-link-wrap {
	height: 500px;
	z-index: 0;
	display: block !important;
	background: linear-gradient(180deg, rgb(255, 255, 255) 0, rgb(247, 247, 247) 100%);
	box-shadow: 0 3px 6px rgba(0, 0, 0, .3);
	min-width: 500px;
	overflow: hidden;
	opacity: 0;
	pointer-events: none;
	padding: 15px 10px 10px;
	border-radius: 2px;
	/* margin-top: -125px; */
	position: fixed;
	top: 50%;
	left: 50%;
	transition: height .2s, margin-top .2s, opacity 0.2s ease-in-out, background 0.3s ease-in-out;
	@include primaryFont(300, $text-color-dark, 13px, 1.4em, normal);
	margin-top: 0 !important;
	margin-left: 0 !important;
	transform: translate(-50%, -50%) !important;

	.dp-info-icon {
		pointer-events: none;
	}

	&.active {
		opacity: 1;
		pointer-events: all;
		z-index: 100000002 !important;

		.dp-info-icon {
			pointer-events: all;
		}
	}

	@include atXS {
		min-width: 440px !important;
		width: 440px !important;
	}

	@include atXXS {
		min-width: 90% !important;
		width: 90% !important;
	}

	@media screen and (max-height: 520px) {
		height: 90%;

	}


	#link-options {
		position: relative;
		padding: 8px 0 12px;
	}

	#dpwplink-url-vars-hint {
		position: absolute;
		top: 50%;
		left: 50%;
		border-radius: 2px;
		overflow: hidden;

		.dpwplink-url-vars-hint-item {
			display: block;
			padding: 5px 10px;

			&:hover {
				//background-color: rgba(var(--dpu-color-1), 0.05);
			}
		}
	}

	.dpwplink-custom-url-vars-hint {
		color: rgba($text-color-dark, 0.9);
		display: none;
	}

	.dpwplink-url-vars-label {

		display: flex;
		padding-top: 10px;


		&>span {
			color: $text-color-dark;
			font-size: 15px;
			font-weight: 600;
			text-transform: uppercase;
		}
	}



	#dp_link-selector {
		padding: 0 16px;
		position: absolute;
		top: 37px;
		left: 0;
		right: 0;
		bottom: 54px;
	}

	#dpwplink-url-vars-wrap {
		padding-bottom: 10px;
	}

	.dpwplink-url-vars {
		display: flex;


		margin-left: -5px;
		margin-right: -5px;
		margin-top: 5px;


		padding: 5px 10px 10px;

		&--system {
			max-width: 100%;
			overflow-x: auto;
			flex-shrink: 0;
			cursor: pointer;
		}

		&--custom {
			max-height: 150px;
			overflow-y: auto;
			flex-wrap: wrap;
		}

		&__item {
			opacity: 0.8;
			transition: all 0.3s ease-in-out;
			background-color: rgba(var(--dpu-color-1), 0.1);

			&:not(.dpwplink-url-vars__item--system) {
				&:nth-child(4n+0) {
					background-color: rgba(var(--dpu-color-2), 0.8);
					color: rgba(var(--dpu-color-2-text), 1);
				}

				&:nth-child(4n+1) {
					background-color: rgba(var(--dpu-color-3), 0.8);
					color: rgba(var(--dpu-color-3-text), 1);
				}

				&:nth-child(4n+2) {
					background-color: rgba(var(--dpu-color-4), 0.8);
					color: rgba(var(--dpu-color-4-text), 1);
				}

				&:nth-child(4n+3) {
					background-color: rgba(var(--dpu-color-1), 0.8);
					color: rgba(var(--dpu-color-1-text), 1);
				}
			}

			&:hover {
				opacity: 1;
			}



			padding: 7px;
			border-radius: 2px;
			align-items: center;
			border-bottom: none;
			width: 44%;
			margin: 5px;

			@include atXXS {
				width: 100%;
			}

			span {
				display: block;
				width: 100%;
				margin-bottom: 5px;
				word-wrap: break-word;
				margin-right: 5px;
				font-weight: 600;
			}

			input {
				width: 100%;

				@include atXS {
					border: 1px solid rgb(118, 118, 118) !important;
				}
			}

			&--id-only {
				width: auto;
				padding: 8px 10px;

				&:hover {
					transform: scale(1.06);
				}

				span {
					margin-bottom: 0px;
				}

				input {
					display: none;
				}
			}

			&--system {
				background-color: rgba(var(--dpu-color-2), 0.1);
				color: $text-color-dark;
				border: 2px solid rgba(var(--dpu-color-2), 1);
				padding: 5px 7px;


				span {
					white-space: nowrap;
				}
			}
		}
	}


	#dp_wp-link {
		border: none !important;
		outline: none !important;
	}

	#dp_wp-link-close {
		color: #666;
		padding: 0;
		position: absolute;
		top: -3px;
		right: 0;
		width: 36px;
		height: 36px;
		text-align: center;
		background: none;
		border: none !important;
		outline: none !important;
		cursor: pointer;
		transition: all $transition-time-fast ease !important;

		&::before {
			font-family: dashicons;
			font-size: 25px;
			width: 36px;
			height: 36px;
			content: "\f158";
			vertical-align: middle;
		}

		&:hover {
			transform: rotate(90deg);
		}
	}


	.link-target,
	.wp-link-text-field {
		display: none !important;
	}

	li {
		clear: both;
		margin-bottom: 0;
		border-bottom: 1px solid #f0f0f1;
		color: #2c3338;
		padding: 4px 6px 4px 10px;
		cursor: pointer;
		position: relative;
	}

	.item-title {
		display: inline-block;
		width: 80%;
		width: calc(100% - 68px);
		word-wrap: break-word;
	}

	.item-info {
		text-transform: uppercase;
		color: #646970;
		font-size: 11px;
		position: absolute;
		right: 5px;
		top: 5px;
	}

	#dp_most-recent-results,
	#dp_search-results {
		top: 140px;
		max-height: 250px;

	}

	#dp_link-modal-title {
		background: none !important;
		text-transform: uppercase;
		border-bottom: none;
		margin: 0;
		padding: 8px 42px 0 16px;
		@include primaryFont(700, $text-color-dark, 18px, 1.3, normal);
	}

	.howto {
		color: #666;
		font-style: italic;
		display: block;
		margin: 3px 0;
		font-size: 13px;
		line-height: 1.1;
		font-family: inherit;
	}

	#dp_most-recent-results,
	#dp_search-results {
		&:focus {
			border-color: rgba(var(--dpu-color-2), 0.7);
			box-shadow: 0 0 0 1px rgba(var(--dpu-color-2), 0.5);
			outline: none;
		}
	}

	.query-results {
		border: 1px #dcdcde solid;
		margin: 0 0 12px;
		background: #fff;
		overflow: auto;
		position: absolute;
		left: 16px;
		right: 16px;
		bottom: 0;

		em {
			font-style: normal;
		}
	}

	ol,
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}

	.river-waiting {
		display: none;
		padding: 10px 0;
	}


	.query-notice {

		.query-notice-hint,
		.query-notice-default {
			border-left: 4px solid rgba(var(--dpu-color-2), 0.9) !important;
			display: block;
			padding: 6px;
		}
	}

	#search-panel,
	#link-options {
		label {
			span:not(.spinner) {
				color: #444;

				font-size: 13px;
				line-height: 1.4em;
				display: none !important;
			}

			input {
				padding: 0 8px;
				line-height: 2;
				min-height: 30px;
				box-shadow: 0 0 0 transparent;
				border-radius: 2px;
				border: 1px solid #7e8993;
				outline: none;
				background-color: #fff;
				color: #32373c;
				font-size: 14px;
				margin: 0 1px;
				min-width: 99%;

				&:focus {
					border-color: rgba(var(--dpu-color-2), 0.7);
					box-shadow: 0 0 0 1px rgba(var(--dpu-color-2), 0.5);
					outline: none;
				}
			}
		}
	}

	#link-options {
		padding-bottom: 8px;
		padding-top: 15px;
	}

	#search-panel {
		margin-top: 0;
	}

	.link-search-wrapper {
		margin-top: 0;

		.spinner {
			z-index: 100000002 !important;
			position: absolute;
			left: 50%;
			transform: translateX(-100%);
			top: 180px;
		}
	}

	li {
		.item-long-info {
			font-weight: 300;
			font-size: 11px;
		}

		&.dpit-special-link {
			font-weight: bold;
			background-color: #dfdfdf;

			&--hidden {
				display: none;
			}
		}

		&:hover {
			background: rgba(var(--dpu-color-2), 0.2);

		}

		&.selected {
			background: rgba(var(--dpu-color-2), 0.8);

			span {
				color: rgba(var(--dpu-color-2-text), 1);
			}

			.item-long-info {
				color: rgba(var(--dpu-color-2-text), 1);
			}
		}


	}

	.submitbox {

		display: flex;
		justify-content: space-between;
		padding: 8px 16px;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;

		.button {
			cursor: pointer !important;
			text-decoration: none !important;
			padding: 10px 15px !important;
			transition: all $transition-time-fast ease-in-out !important;
			outline: none !important;
			border: 2px rgba(#666, 0.6) solid !important;
			color: rgba($text-color-dark, 0.9) !important;
			opacity: 0.9 !important;
			//border-radius: 100px !important;
			box-shadow: none !important;
			@include primaryFontImportant(600, $text-color-light, 15px, 15px, normal, center);
			text-transform: uppercase !important;

			@include atXXXS {
				font-size: 13px !important;
				padding: 8px 12px !important;
			}

			&.hovered,
			&:hover {
				opacity: 1 !important;

			}

			&.button-primary {
				border: none !important;
				font-weight: 700 !important;
				color: rgba(var(--dpu-color-g-text), 1) !important;
				padding: 12px 30px !important;
				background: linear-gradient(315deg, rgba(var(--dpu-color-g), 0.71) 0%, rgba(var(--dpu-color-g), 0.8) 20%, rgba(var(--dpu-color-g), 0.86) 100%) !important;
				background-color: var(--dpu-color-g) !important;

				@include atXXXS {
					padding: 8px 17px !important;
				}
			}

			&.disabled {
				opacity: 0.35 !important;
				cursor: default !important;
				pointer-events: none !important;
			}
		}

		#dp_wp-link-cancel {
			.button {
				color: rgba($text-color-dark, 0.9) !important;
				background: unset !important;

				&.hovered,
				&:hover {

					background-color: rgba(var(--dpu-color-r), 0.9) !important;
					color: rgba(var(--dpu-color-r-text), 0.9) !important;
					border-color: rgba(var(--dpu-color-r), 0.9) !important;
				}
			}
		}

		#dp_wp-link-update {


			.spinner {
				pointer-events: none;
			}

			.button {
				float: right;

				@include atXXXS {
					// max-width: 200px;
				}

			}
		}
	}

	.dpwplink-inactive-el {
		display: none;
	}
}


.wp-admin #dp_wp-link-wrap.dp_wp-link-wrap {
	border-radius: 2px;

	.submitbox .button {
		border-radius: 2px !important;
	}
}





