@import '../../../includes/assets/css/consts.scss';
@import '../../../includes/assets/css/partials/admin-theme-colors';
@import '../../../includes/assets/css/partials/animations';
@import '../../../includes/assets/css/partials/dwplink';
@import '../../../includes/assets/css/modules/dp-mobile-view-popup';
@import './partials/dpwplink-public';
@import './partials/dp-tmce-dialog-public';
@import '../../../includes/assets/css/modules/dp-info-icon';
@import '../../../includes/assets/css/modules/tippy';
@import '../../../includes/assets/css/partials/pro-promo';
@import '../../../includes/assets/css/modules/dp-tmce-dialog';


#tippy-1,
#tippy-2,
#tippy-3,
#tippy-4 {
	@include z-index(16, true);
	//z-index: 2147483647 !important;
}

html.dpit-mm {
	#wpadminbar {
		z-index: 0;
	}
}

.dpit-wrapper.dpit_builder {
	--dpit-shadow-color: 30, 30, 30;
	--dpit-shadow-alpha: 0.3;


	--secondary-font: 'Arial', Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

	.dpit-button {

		&--skip,
		&--done {
			cursor: not-allowed;
		}
	}

	&.dpit-is-sticky-theme {
		.dpit-tooltip {
			&--on-top {
				.dpit-button {
					&--skip {
						display: none;
					}
				}
			}
		}
	}

}