@import '../base/mixins';

#dp_wp-link-wrap.dp_wp-link-wrap {


	&.no-pointer-events {
		pointer-events: none;
		background: linear-gradient(140deg, rgba(255, 255, 255, 0.6) 0, rgba(255, 255, 255, 0.6) calc(100% - 150px), rgba(255, 255, 255, 0.85) calc(100% - 75px), rgba(255, 255, 255, 1) 100%);
	}


	&.active {
		@include z-index(5, true);
	}



	#dp_most-recent-results,
	#dp_search-results {
		top: 140px;
		max-height: 230px;
	}

	#dp_wp-link-update,
	.link-search-wrapper {
		.spinner {

			//z-index: 100000002 !important;
			@include z-index(5, true);
			background: url(../../../../../../wp-admin/images/spinner.gif) no-repeat !important;
			background-size: 20px 20px !important;
			background-position: center !important;
			vertical-align: middle !important;

			position: absolute !important;
			left: 50% !important;
			transform: translateX(-50%) !important;
			top: 175px !important;
			filter: alpha(opacity=70) !important;
			width: 20px !important;
			height: 20px !important;
			display: inline-block !important;
			visibility: visible !important;
			opacity: 0 !important;
			transition: opacity 0.4s ease-in-out !important;

			&.is-active {
				opacity: .7 !important;
			}
		}
	}

	#dp_wp-link-update {
		position: relative !important;


		#dp_wp-link-submit {
			transition: all 0.7s ease !important;

			&.spinner-bg {
				opacity: 0 !important;
			}
		}

		.spinner {

			top: 50% !important;
			left: unset !important;
			right: 0% !important;
			transform: translate(-30%, -50%) !important;
			background: url(../../../../../../wp-admin/images/spinner-2x.gif) no-repeat !important;
			background-size: 40px 40px !important;
			width: 40px !important;
			height: 40px !important;
		}

	}
}

#dp_wp-link-backdrop {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	min-height: 360px;
	background: #000;
	filter: alpha(opacity=70);
	//z-index: 100100;
	@include z-index(-2, true);
}

#wp-dpwplink-editor-public-wrap {
	opacity: 0;
	pointer-events: none;
}